/**
 * @hidden
 */
export const isPresent: Function = (value: any): boolean => value !== null && value !== undefined;

/**
 * @hidden
 */
export const isBlank: Function = (value: any): boolean => value === null || value === undefined;

/**
 * @hidden
 */
export const isArray: Function = (value: any): boolean => Array.isArray(value);

/**
 * @hidden
 */
export const isTruthy: Function = (value: any): boolean => !!value;

/**
 * @hidden
 */
export const isNullOrEmptyString: Function = (value: string): boolean => isBlank(value) || value.trim().length === 0;

/**
 * @hidden
 */
export const isChanged: Function = (propertyName: string, changes: any): boolean => (
    changes[propertyName] && !changes[propertyName].isFirstChange() &&
        changes[propertyName].previousValue !== changes[propertyName].currentValue
);



